import { useEffect, useState } from "react";

import { Elements } from "@stripe/react-stripe-js";
import CheckoutForm from "./CheckoutForm";
import { loadStripe } from "@stripe/stripe-js";

function Payment() {
  const [stripePromise, setStripePromise] = useState(null);
  const [clientSecret, setClientSecret] = useState("");
  const [amount, setAmount] = useState();
  const [amountAdded,setAmountAdded] = useState(false)

  useEffect(() => {
    fetch("/config").then(async (r) => {
      const { publishableKey } = await r.json();
      setStripePromise(loadStripe(publishableKey));
    });
  }, []);

  function setAmountHandler() {
    fetch("/create-payment-intent", {
      method: "POST",
      headers: {
        'Content-Type':'application/json',
        'Accept':'application/json'
    },
      body: JSON.stringify({amount: amount}),
    }).then(async (result) => {
      var { clientSecret } = await result.json();
      setClientSecret(clientSecret);
    });
    setAmountAdded(true)
  }

  // useEffect(() => {
    
  // }, []);

  if (!amountAdded) {
    return (
      <div align='center'>
        <h1>Universal Traders</h1>
        <input style={{borderRadius: 5,width: '90%',padding: 10}} placeholder="Amount" type="number" onChange={(e) => {
        setAmount(e.target.value)
        }}></input>
        <div align='right' style={{width: '92%'}}>
          <button onClick={() => {
            setAmountHandler();
          }}>Pay {amount ? amount : '' }</button>
        </div>
      </div>
    )
  }

  return (
    <>
      <div align='center'>
        <h1>Universal Traders</h1>
      </div>
      {clientSecret && stripePromise && (
        <Elements stripe={stripePromise} options={{ clientSecret }} >
          <CheckoutForm amount={amount} />
        </Elements>
      )}
    </>
  );
}

export default Payment;
